.prod-main-ctn {
    position: relative;
    width: 100%;
    height: fit-content;
}

.prod-nav {
    height: 80px;
    position: fixed;
    top: 100px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding : 0 30px;
}

.filter-nav {
    width: fit-content;
    height: fit-content;
    padding: 5px 22px;
    border-radius: 4px;
    background-color: #df1300;
    border: solid 2px #df1300;
    font-size: 14px;
    font-weight: 400;
    color: rgb(244, 244, 244);
    cursor: pointer;
    user-select: none;
    transition: all;
    transition-duration: .4s;

}
.filter-nav:hover {
    background-color: inherit;
    border: solid 2px #df1300;
    color: #df1300;
}

.filter-div {
    z-index: 3;
    border: solid 1px rgba(195, 195, 195, 0.44);
    padding: 20px 20px;
    border-radius: 6px;
    width: 350px;
    height: 400px;
    background-color: #f5fdff;
    position:fixed;
top:170px;
left: 20px;
transition: all;
transition-duration: .4s;
}
.filter-div-cancel {
    transform: scale(0);
}

.category-title {
    text-align: left;
    font-size: 17px;
    font-weight: 400;
}
.category {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-top: 25px;
    gap:16px
}
.cate-list {
    font-size: 14px;
    font-weight: 500;
    color: rgb(65, 65, 65);
    border: solid 1px rgba(195, 195, 195, 0.644);
    padding: 7px 32px;
    cursor: pointer;
    user-select: none;
    transition: all;
    transition-duration: .4s;
}
.cate-list:hover {
    color: #E73810;;
    border: solid 1px #E73810;

}
.size-title {
    text-align: left;
    font-size: 17px;
    font-weight: 400;
    margin-top: 27px;
}
.sizes {
    margin-top: 30px;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.size-list {
    font-size: 14px;
    font-weight: 500;
    color: rgb(65, 65, 65);
    border: solid 1px rgba(195, 195, 195, 0.644);
    padding: 7px 15px;
    cursor: pointer;
    user-select: none;
    transition: all;
    transition-duration: .4s;
}
.size-list:hover {
    color: #E73810;
    border: solid 1px #E73810;

}

.view-nav {
    width: fit-content;
    height: fit-content;
    padding: 5px 22px;
    border-radius: 4px;
    background-color: #df1300;
    border: solid 2px #df1300;
    font-size: 14px;
    font-weight: 400;
    color: rgb(244, 244, 244);
    cursor: pointer;
    user-select: none;
    transition: all;
    transition-duration: .4s;

}
.view-nav:hover {
    background-color: inherit;
    border: solid 2px #df1300;
    color: #df1300;
}

.view-div {
    z-index: 3;
    border: solid 1px rgba(195, 195, 195, 0.44);
    padding: 20px 20px;
    border-radius: 6px;
    width: 200px;
    height: 140px;
    background-color: #f5fdff;
    position:fixed;
    top:170px;
    /* left: 420px; */
    left: -100px;
    right: 0;
    margin: auto;
    transition: all;
    transition-duration: .4s;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap:15px;
}
.view-div-cancel {
    transform: scale(0);
}

.list {
    font-size: 14px;
    font-weight: 500;
    color: rgb(65, 65, 65);
    border: solid 1px rgba(195, 195, 195, 0.644);
    padding: 7px 45px;
    cursor: pointer;
    user-select: none;
    transition: all;
    transition-duration: .4s;
}
.list:hover {
    color: #E73810;
    border: solid 1px #E73810;
}

.srh-nav {
    width:220px;
    height: 37px;
}
.srh-input {
    border-radius: 4px;
    border: rgba(162, 162, 162, 0.461) solid 1px;
    background-color:rgb(249, 249, 249);
    width: 100%;
    height: 100%;
    padding:10px;
    font-size: 14px;
    font-weight: 400;
}
.prod-list-ctn {
    top: 200px;
    position: absolute;
    width: 100%;
}
.prod-table-ctn {
    height: 350px;
}

.prod-item-ctn {
    display: grid;
    grid-template-columns: 250px 250px 250px 250px;
    width: fit-content;
    margin: 0 auto;
    margin-top: 200px;
    padding: 10px 15px;
    gap:27px;
}
.prod-item {
    height: 400px;
    background-color: rgb(225, 239, 255);
    border-radius: 9px;
    cursor: pointer;
    user-select: none;
}
.prod-img:hover{
    height: 245px;
}
.prod-img {
    object-fit: cover;
    object-position: center;
    width: 100%;
    height: 230px;
    transition: all;
    transition-duration: .4s;
}
.prod-title {
    font-size: 15px;
    font-weight: 500;
    color: rgb(33, 33, 33);
    padding:10px 5px;
}
.prod-category{
    font-size: 13px;
    font-weight: 400;
    color: rgb(62, 62, 62);
    padding:10px 5px;
}
.prod-price{
    font-size: 13px;
    font-weight: 600;
    color: rgb(33, 33, 33);
    padding:10px 5px;
}

