
.Prod-edit-main-ctn {
    width: 100%;
    position: relative;
    height: 140vh;
}
.prod-edit-main-ctn {
    width: 85%;
    height:570px;;
    background-color: white;
    position: absolute;
    top: 100px;
    right: 0;
    left: 0;
    margin: auto;
    margin-bottom: 20px;
    padding: 25px 30px;
    
}
.bck-icn {
    cursor: pointer;
    user-select: none;
}
.ctn-one {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.ctn-two {
    margin-top: 50px;
    align-items: flex-start;
}
.prod-edit-img-ctn {
    position: relative;
    width: fit-content;
}

.prod-edit-img{
    width: 270px;
    height: 240px;
    object-fit: cover;
    object-position: center;
    border-radius: 9px;
    filter: brightness(.5);
    transition: all;
    transition-duration: .4s;
}
.prod-edit-img-empty {
    background-color: rgb(213, 213, 213);
}
.prod-edit-img:hover{
    filter:brightness(.6);
}
.prod-edit-icn {
    position: absolute;
    top:0;
    right: 0;
    left: 0;
    bottom: 0;
    margin: auto;
    color: #eaeaea;
    border-radius: 50%;
    width: fit-content;
    height: fit-content;
    padding: 13px;
    border:solid 2px #eaeaea;
    font-size:18px;
    z-index: 2;
    cursor: pointer;
    user-select: none;
    transition: all;
    transition-duration: .4s;
}
.prod-edit-icn:hover{
    transform: rotate(360deg);
}

.prod-edit-input-ctn {
    width: 310px;
    /* height: 300px; */
}
.name-input-ctn {
    width:100%;
    height:35px;
    margin-bottom: 43px;
}
.size-cate-ctn {
    display: flex;
    align-items: center;
    gap: 10px;
    width: fit-content;
}
.prod-edit-cate-ctn{
    width: 200px;
    height: 35px;
}
.prod-edit-size-ctn{
    width: 80px;
    height: 35px;
}
.prod-edit-btn {
    margin-top: 30px;
    width: 200px;
    height: fit-content;
    padding: 10px 0;
    border-radius: 5px;
    background-color: rgb(204, 16, 16);
    color: rgb(240, 240, 240);
    font-size: 15px;
    font-weight: 500;
    transition: all;
    transition-duration: .4s;
}
.prod-edit-btn:hover{
    background-color:rgb(254, 40, 40);
}

@media (max-width: 800px) {
    
.Prod-edit-main-ctn {
    width: 100%;
    position: relative;
    height: 170vh;
}
.prod-edit-main-ctn {
    width: 85%;
    height:880px;;
    background-color: white;
    position: absolute;
    top: 100px;
    right: 0;
    left: 0;
    margin: auto;
    margin-bottom: 20px;
    padding: 25px 30px;
    
}

.prod-edit-img-ctn {
    position: relative;
    width: fit-content;
    margin-bottom: 20px;
}

.prod-edit-input-ctn {
    width: 310px;
    /* height: 300px; */
    margin-top: 20px;
}
    
.size-cate-ctn {
    display: flex;
    align-items: center;
    gap: 10px;
    width: fit-content;
    margin-top: 100px;
}

.ctn-one {
    display: block;
    justify-content: space-between;
    align-items: center;
    margin:0 auto;
    width: fit-content;
}
.prod-edit-btn {
    margin-top: 80px;
    width: 100%;
    height: fit-content;
    padding: 10px 0;
    border-radius: 5px;
    background-color: rgb(204, 16, 16);
    color: rgb(240, 240, 240);
    font-size: 15px;
    font-weight: 500;
    transition: all;
    transition-duration: .4s;
}
}

@media (max-width: 424px) {
    
.prod-edit-input-ctn {
    width: 100%;
    /* height: 300px; */
    margin-top: 20px;
}

.prod-edit-cate-ctn{
    width: 160px;
    height: 35px;
}
.prod-edit-size-ctn{
    width: 80px;
