.user-ctn {
    margin-top: 100px;
    
}
.user-title {
    text-align: center;
    font-size: 16px;
    font-weight: 500;
    margin: 50px 0;
}

.create-table-ctn {
    margin-top: 20px;
    display:  block;
    width: 95%;
    margin:0 auto;
    padding: 10px 0px;
}

.user-table-ctn {
    width: 100%;
    background-color: rgb(255, 255, 255);
    height: 380px;
    position: relative;
    overflow: auto;
}
/* width */
::-webkit-scrollbar {
    width: 5px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    background: inherit; 
  }
   
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #900000; 
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #a73333; 
  }
  .thead {
    z-index: 2;
    width: 100%;
    display: flex;
    padding: 10px 20px;
    /* justify-content: space-between; */
    /* gap:150px; */
    align-items: center;
    position: sticky;
    top:0;
    background-color: #6f0505;
    color:rgb(228, 228, 228);
}
.tbody {
    width: 100%;
    height: fit-content;
}
.userdata {
    overflow: hidden;
    width: 100%;
    height: 40px;
    background-color: #f5f5f5;
    display: flex;
    padding: 30px 20px;
    border:solid rgba(137, 137, 137, 0.522) 1px;
    /* justify-content: space-between; */
    /* gap:50px; */
    align-items: center;
    font-size: 14px;
    cursor: pointer;
    user-select: none;
    transition: all;
    transition-duration: .3s;
    transition-timing-function: ease-in-out;
    position: relative;
}
.userdata:hover{
    background-color: #e6e6e6;
}
.edit-btn {
    position: absolute;
    right: 0;
    top:0;
    width: 150px;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color:rgb(255, 255, 255);
    transition: all;
    transition-duration: .4s;
}
.edit-btn-cen {
    position: absolute;
    right: -170px;
    top:0;
    width: 150px;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color:rgb(255, 255, 255);
    transition: all;
    transition-duration: .4s;
}
.edit-btn-icn {
    width: 50%;
    font-size: 20px;
    color:rgb(61, 61, 61);
}
.edit-ctn:hover {
    background-color: #f33333;
    color: rgb(231, 231, 231)
}
.del-ctn:hover {
    background-color:#f33333;
    color: rgb(231, 231, 231)
}
