/*Create User Container */

.create-user-ctn {
    background-color:yellow;
    display: flex;
    align-items: center;
    width: 30%;
    height:fit-content;
    padding: 10px 20px;
    border-radius: 9px;
    margin:0 auto;
    background-image:linear-gradient(to left ,white ,rgb(255, 218, 218),white);
}

