.main-ctn {
position: absolute;
top: 100px; 
left: 0;
right: 0;
margin: auto;
width: fit-content;
padding: 20px 50px;
height: 590px;
background-color: rgb(255, 255, 255);
  padding: 20px;

}
.back-btn {
    width: fit-content;
    height: fit-content;
    color: red;
    margin-bottom: 30px;
    cursor:pointer ;
    user-select: none;
}
.title {
    color: rgb(61, 61, 61);
    font-size: 18px;
    font-weight: 400;
    text-align: left;
    margin-bottom: 30px;
}
.inputs-main-ctn {
    width:280px;
}
.username-input-prt {
    margin-bottom: 33px;
}

.username-title {
    color: rgb(68, 68, 68);
    font-size: 15px;
    font-weight: 500;
    margin-bottom: 10px;
}
.username-input-ctn {
    width: 100%;
    height: 37px;
}
.username-input {
    border-radius: 4px;
    border: rgba(162, 162, 162, 0.461) solid 1px;
    background-color:rgb(249, 249, 249);
    width: 100%;
    height: 100%;
    padding:10px;
    font-size: 14px;
    font-weight: 400;
}
.update-btn{
    margin-top: 20px;
    width:100%;
    /* height: 30px; */
    border-radius: 5px;
    background-color: rgb(204, 16, 16);
    color: rgb(240, 240, 240);
    font-size: 15px;
    font-weight: 500;
    padding: 10px 0;
}
.note {
    padding: 30px 1px;
    width: 100%;
    color: rgb(252, 11, 11);
    font-size: 14px;
    font-weight: 500;
}