.dash-main-ctn {
    position: relative;
    height: 200vh;
    width: 100%;
    overflow: hidden;
}
.dash-layer {
    position: absolute;
    width: 80%;
    height: 40%;
    /* transform: rotate(-43deg) translateY(-200px) translateX(600px); */
    left: 0;
    top: 0;
    border-bottom-right-radius: 300px;
}

.dashboard-title {
    position: absolute;
    left: 0;
    padding: 0 30px;
    color:#f5f5f5;
    font-size: 18px;
    font-weight: 500;
}

.admin-card-ctn {
    position: absolute;
    z-index: 2;
    top: 160px;
    left: 0px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between ;
    padding: 0 30px;
    align-items: center;
    width: 100%;
}
.admin-card {
    background-color: #fff;
    padding: 20px 30px ;
    border-radius: 10px;
    min-width: 30%;
}

.admin-card-data-ctn {
    display: flex;
    gap: 15px;
    align-items: center;
    margin-top: 15px;
}
.admin-chart-ctn {
    width: 100%;
    padding: 20px 30px ;
    position: absolute;
    top: 500px;
    height: fit-content;
    display: flex;
    justify-content: space-between;
}
.user-chart-ctn {
    background-color: #fff;
    width:55%;
    height: 480px;
    border-radius: 8px;
    padding: 5px 30px;
    padding-top: 100px;
    /* color: ; */
}

.product-chart-ctn {
    width: 40%;
    background-color: #fff;
    height: 480px;
    border-radius: 8px;
    padding: 25px 30px;
    padding-top: 100px;
}

/*Table*/

.order-table-ctn {
    width: 95%;
    margin: auto;
    text-align: left;
    position: relative;
}

#order-table {
    width: 100%;
    height:  400px;
    overflow: scroll;
    margin-bottom: 60px;
} 

.table-head {
    background-color: #dfdfdf;
    margin:10px 20px;
    font-size : 14px;
}
.table-body {
    background-color: rgba(255, 255, 255, 0.634);
    margin:10px 20px;
    font-size : 14px;

}


