.nav-main-ctn {
    position: fixed;
    top: 0;
    background-color: #ff8b8b18;
    width: 100%;
    height: fit-content;
    z-index: 4;
}

.menu-icnn {
    width: fit-content;
    height: fit-content;
    padding: 9px;
    border-radius: 50%;
    background-color: #fdf1f1;
    color:rgb(50, 50, 50);
}

.nav-ctn {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding:20px 33px;
}
.menu-bar-ctn {
    width: 250px;
    height: 100vh;
    position: fixed;
    left: 0;
    top: 0;
    z-index: 4;
    background-color: rgb(236, 232, 223);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap:25px;
    transition: all;
    transition-duration: .4s;
}

.menu-bar-layer {
    height: 1px;
    background-color: rgba(62, 62, 62, 0.503);
    width: 77%;
}
.cancel-btn {
    position: absolute;
    top:10px;
    right: 30px;
    width: fit-content;
    height: fit-content;
    margin: auto;
    color: rgb(52, 52, 52);
    font-size: 20px;
}

.menu-bar-ctn-off {
    width: 0%;
    height: 100vh;
    position: fixed;
    left: -100px;
    top: 0;
    z-index: 4;
    background-color: rgb(236, 232, 223);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap:25px;
    transition: all;
    transition-duration: .4s;
}

.nav {
    display: flex;
    gap: 22px;
    align-items: center;
    justify-content: center;
}


.admin-pfp {
    width: 35px;
    height: 35px;
    border-radius: 50%;
    object-fit: cover;
    object-position: center;
    transition: all;
    transition-duration: .4s;
}
.admin-pfp:hover {
    filter: brightness(.8);
}

.pfp-menu-bar-ctn {
    width: 300px;
    height: 70vh;
    background-color: #fff;
    position: fixed;
    right: 20px;
    border-radius: 20px;
}

.pfp-menu-pfp {
    width: 80px;
    height: 80px;
    border-radius: 50%;
    position: absolute;
    margin:auto;
    left: 0;
    right: 0;
    top: 40px;
    object-fit: cover;
    object-position: center;
}

.edit-icn {
    position: absolute;
    top: 110px;
    right:120px;
}

.pfp-menu-name {
    padding:2px;
    text-align: center;
    margin-top: 150px;
    font-size: 16px;
    font-weight: 500;
}
.name-edit-input {
    width: 100%;
    height: 30px;
    border: solid 2px rgba(95, 95, 95, 0.34);
    border-radius: 5px;
    margin:auto;
    padding: 18px 12px;
    font-size: 16px;
    font-weight: 500;
    background-color: #fff8f0;

}

.name-edit-input-ctn {
width:fit-content;
margin: auto;
margin-top: 150px;
}

.pfp-menu-btn-ctn {
    opacity: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-top: 90px;
    transition: all;
    transition-duration: .4s;
    transition-timing-function: ease-out;
}
.pfp-menu-bar-ctn-off {
    display: none;
    transition: all;
    transition-duration: .4s;
    transition-timing-function: ease-out;
}

.pfp-menu-btn {
    width: 65%;
    border-radius: 5px;
    height: fit-content;
    padding : 9px ;
    margin-bottom: 20px;
    font-size: 14px;
    transition: all;
    transition-duration: .4s;
    transition-timing-function: ease-out;
    cursor: pointer;
    user-select: none;
}

.pfp-menu-btn-1 {
    background-color: #f33333;
    color: rgb(242, 239, 239);
}

.pfp-menu-btn-1:hover {
    background-color: #c70000;

}
.pfp-menu-btn-2 {
    background-color: #ebebeb;
    color: rgb(45, 45, 45);
}
.pfp-menu-btn-2:hover {
    background-color: #d1cece;
}
hv-icn {
    transition-timing-function: ease-in;
    transition: all;
    transition-duration: .4s;
}
.hv-icn:hover {
    color: rgb(171, 3, 3);
}

